$positive: white;
$negative: black;
$theme-1-primary: rgb(93, 202, 202);
$theme-2-primary: rgb(207, 72, 207);
$theme-3-primary: rgb(192, 192, 59);
$theme-4-primary: gainsboro;
$line-color: rgb(197, 77, 77);

// light mode
$ipositive: black;
$inegative: white;
$itheme-1-primary: rgb(162, 53, 53);
$itheme-2-primary: rgb(48, 183, 48);
$itheme-3-primary: rgb(63, 63, 196);
$itheme-4-primary: rgb(169, 169, 169);
$iline-color: rgb(58, 178, 178);

$box-size: 84px;

@font-face {
    font-family: "VCR";
    src: url("../VCR_OSD_MONO.ttf");
}

@font-face {
    font-family: "MajorMono";
    src: url("../MajorMonoDisplay-Regular.ttf");
}

$fonty: 'Courier New', Courier, monospace;

html {
    scroll-behavior: smooth;
}

:root
{
    --positive: #{$positive};
    --negative: #{$negative};
    --theme-1-primary: #{$theme-1-primary};
    --theme-2-primary: #{$theme-2-primary};
    --theme-3-primary: #{$theme-3-primary};
    --theme-4-primary: #{$theme-4-primary};
    --line-color: #{$line-color};
}

.dark
{
    --positive: #{$positive};
    --negative: #{$negative};
    --theme-1-primary: #{$theme-1-primary};
    --theme-2-primary: #{$theme-2-primary};
    --theme-3-primary: #{$theme-3-primary};
    --theme-4-primary: #{$theme-4-primary};
    --line-color: #{$line-color};
}

.light
{
    --positive: #{$ipositive};
    --negative: #{$inegative};
    --theme-1-primary: #{$itheme-1-primary};
    --theme-2-primary: #{$itheme-2-primary};
    --theme-3-primary: #{$itheme-3-primary};
    --theme-4-primary: #{$itheme-4-primary};
    --line-color: #{$iline-color};
}

*
{
    margin: 0px;
    padding: 0px;
    
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */

    //cursor: cell;
}

body
{
    background-color: var(--negative);
    transition: background-color 1.5s ease-in;
    pointer-events: none;
    font-family: $fonty;
}

body.games
{
    transition: background-color 0.8s ease-in;
    background-color: darken($theme-1-primary, 30%);
    
}

body.music
{
    transition: background-color 0.8s ease-in;
    background-color: darken($theme-2-primary, 30%);
}

body.art
{
    transition: background-color 0.8s ease-in;
    background-color: darken($theme-3-primary, 30%);
}

body.me
{
    transition: background-color 0.8s ease-in;
    background-color: darken($theme-4-primary, 30%);
}

body.games.light
{
    transition: background-color 0.8s ease-in;
    background-color: darken($itheme-1-primary, 0%);
    
}

body.music.light
{
    transition: background-color 0.8s ease-in;
    background-color: darken($itheme-2-primary, 10%);
}

body.art.light
{
    transition: background-color 0.8s ease-in;
    background-color: darken($itheme-3-primary, 0%);
}

body.me.light
{
    transition: background-color 0.8s ease-in;
    background-color: darken($itheme-4-primary, 0%);
}

#background
{
    pointer-events: none;
    background-image: url("../static.gif");
    background-size: cover;
    background-position: center;
    position: fixed;
    opacity: 30%;
    width: 100%;
    height: 100%;
    z-index: -10;
}

.layer
{
    background-color: var(--negative);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /*transition: 10s ease-in;*/
    animation: bg_layer_fade 4s ease-in forwards;
    pointer-events: none;
    z-index: 100;
}

#title
{
    pointer-events: all;
    font-weight: normal;
    font-family: $fonty;
    position: fixed;
    
    text-decoration: none;
    align-items: center;
    justify-content:center;
    display: flex;

    padding: 10px;
    z-index: 3;
    
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */

    &:hover div {
        transition: text-shadow 1s ease-out;
        text-shadow: var(--theme-1-primary) 5px 10px, var(--theme-2-primary) 10px 20px, var(--theme-3-primary) 15px 30px;
        font-weight: bold;
    }

    div {
        font-size: 32px;
        font-style: italic;
        color: var(--positive);
        position: relative;
        display: flex;
        transition: text-shadow 0.2s ease-in;
    }
}

#color-mode
{
    position: fixed;
    bottom: 30px;
    left: 20px;
    overflow: hidden;

    svg {
        color: var(--positive);

        &.current-mode {
            display: block;
        }
        &.other-mode {
            display: none;
        }
    }

    &:hover {
        svg {
            &.current-mode {
                display: none;
            }
            &.other-mode {
                display: block;
            }
        }
    }
}

#main-nav
{
    position: fixed;
    pointer-events: none;
    height: 100vh;
    width: 100vw;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: height 0.8s ease-in-out, width 0.8s ease-in-out;
    overflow: hidden;
    z-index: 5;

    li {
        transform: translateZ(0);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 4vw;
    }

    div{
        &.float {
            z-index: 1;
            display: flex;
            flex-direction: row;
            margin: 20px;
            transition: 0.2s ease-in;
            animation: floaty 3s ease-in-out infinite;
            text-align: center;
        }

        a p {
            position: absolute;
            color: var(--positive);
            font-size: 32px;
            transition: 0.2s ease-in;
            width: 60%;
            margin: 0px auto;
            writing-mode: vertical-rl;
            -webkit-writing-mode: vertical-rl;
            -ms-writing-mode: vertical-rl;
            text-align: center;
            vertical-align: center;
            opacity: 100%;
        }

        &:hover a p {
            transition: 0.2s ease-in;
            opacity: 100%;
        }
    }

    a {
        color: var(--positive);
        text-decoration: none;
        position: relative;
        font-family: $fonty;
        font-size: 24px;
        white-space: nowrap;
        z-index: 1;
        text-align: center;
        font-style: normal;
        margin: 0px;
        padding: 0px;
    }

    &.compact {
        height: 10vh;
        transition: height width 0.3s ease-in-out;
        li {
            padding: 1vw;
        }
    }

    &.compact div.float {
        margin: 0px;
        transition: 0.3s ease-in-out;
        text-align: center;
    }
    
    li {
        transition: 1s ease-in-out;
        &:hover {
            transform: translate(-23px, 0px);
            transition: 1s ease-in-out;
        }
    }

    .Icon {
        font-size: 60px;
        padding: 0px;
        margin: 0px;
        transition: text-shadow 0.2s ease-in, font-size 0.4s ease-in;
    }

    .box-label {
        color: var(--negative);
        letter-spacing: 10px;
        text-decoration: none;
        position: relative;
        font-family: $fonty;
        font-size: 64px;
        margin: 0;
        white-space: nowrap;
        z-index: 1;
        text-align: center;
        font-style: normal;
        width: 100%;
        height: 0px;
        opacity: 0%;
        writing-mode: vertical-rl;
        -webkit-writing-mode: vertical-rl;
        -ms-writing-mode: vertical-rl;
        -webkit-user-select: none; /* Safari */        
        -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* IE10+/Edge */
        user-select: none; /* Standard */
        pointer-events: none;
        transition: letter-spacing 0.8s ease-in, height 0.8s ease-in, opacity 0.8s ease-in, margin 0.8s ease-in;
    }

    .hover-content {
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: column;
        align-content: center;
        align-items: center;
        pointer-events: all;
        transition: 1.8s ease-in;
    }

    .shadow-bar
    {
        width: 100%;
        height: $box-size;
        padding: 0px;
        margin: 0px;
        gap: 50vh;
        display: flex;
        flex-direction: row;
        align-content: center;
        justify-content: center;
        transform: rotate(90deg);
        transition: 1s ease-in;
        opacity: 100%;

        li {
            white-space: nowrap;
            display: inline-block;
            width: fit-content;
            height: 100%;
            padding: 0px;
            margin: 0px;
            transition: 0.4s ease-in-out;
            a {
                color: var(--negative);
                font-family: $fonty;
                font-size: 48px;
                padding: 0px 20px;
                margin: 0px;
            }
            &:hover {
                transition: 0.2s ease-in-out;
                transform: translate(-10px,10px);
            }
        }
    }

    .shadow {
        margin-left: -$box-size;
        padding: 0;
        
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        opacity: 0%;
        width: $box-size;//52px;
        height: 200vh;
        pointer-events: none;
        transition: 1s ease-in;
        transform: rotateY(-90deg) translateZ(-$box-size + $box-size / 2);
        z-index: 1;
    }

    #games{
        .shadow {
            background-color: desaturate(darken($theme-1-primary, 10%), 20%);
        }

        .shadow-bar li {
            background-color: darken($theme-1-primary, 20%);

            &:hover {
                background-color: darken($theme-1-primary, 4%);
                box-shadow: darken($theme-1-primary, 20%) 10px -10px;
            }
        }

        &:hover .hover-bg
        {
            padding: 2px;
            background-color: rgba($theme-1-primary, 0.5);/*var(--theme-1-primary);*/
            //height: 200vw;
            transition: background-color 0.2s ease-in-out, height 0.5s ease-in-out;
            pointer-events: all;
        }
    }

    #music{
        .shadow {
            background-color: desaturate(darken($theme-2-primary, 10%), 20%);
        }

        .shadow-bar li {
            background-color: darken($theme-2-primary, 20%);

            &:hover {
                background-color: darken($theme-2-primary, 4%);
                box-shadow: darken($theme-2-primary, 20%) 10px -10px;
            }
        }

        &:hover .hover-bg {
            padding: 2px;
            background-color: rgba($theme-2-primary, 0.5);
            height: 200vw;
            transition: background-color 0.2s ease-in-out, height 0.5s ease-in-out;
            pointer-events: all;
        }
    }

    #art {
        .shadow {
            background-color: desaturate(darken($theme-3-primary, 10%), 20%);
        }

        .shadow-bar li {
            background-color: darken($theme-3-primary, 20%);

            &:hover {
                background-color: darken($theme-3-primary, 4%);
                box-shadow: darken($theme-3-primary, 20%) 10px -10px;
            }
        }

        &:hover .hover-bg {
            padding: 2px;
            background-color: rgba($theme-3-primary, 0.5);
            height: 200vw;
            transition: background-color 0.2s ease-in-out, height 0.5s ease-in-out;
            pointer-events: all;
        }
    }

    #me {
        .shadow {
            background-color: desaturate(darken($theme-4-primary, 10%), 20%);
        }

        .shadow-bar li {
            background-color: darken($theme-4-primary, 20%);

            &:hover {
                background-color: darken($theme-4-primary, 4%);
                box-shadow: darken($theme-4-primary, 20%) 10px -10px;
            }
        }

        &:hover .hover-bg {
            padding: 2px;
            background-color: rgba($theme-4-primary, 0.5);
            height: 200vw;
            transition: background-color 0.2s ease-in-out, height 0.5s ease-in-out;
            pointer-events: all;
        }
    }

    :hover {
        .box-label
        {
            letter-spacing: 20px;
            white-space: nowrap;
            z-index: 1;
            text-align: center;
            font-style: normal;
            transition: 0.5s ease-in-out;
            margin-top: 20px;
            height: 300px;
            opacity: 100%;
            pointer-events: all;
        }

        .shadow
        {
            pointer-events: all;
            opacity: 60%;
            height: 200vh;
            transition: 0.5s ease-in-out;
            .shadow-bar
            {
                //transform: rotate(90deg) skewX(40deg) scaleY(0.8);
                transition: 0.5s ease-in-out;
                opacity: 100%;
                gap: 0px;
            }
        }

        .rotate
        {
            //-ms-transform: rotate(-20deg) skewy(15deg) scaleX(0.9);
            //transform: rotate(-20deg) skewy(15deg) scaleX(0.9);
            transform: rotateY(30deg) rotateX(20deg);
            transition: 0.5s ease-in-out;

            &.showmenu
            {
                transform: rotateY(70deg) rotateX(20deg);
                transition: 0.3s ease-in-out;
            }
        }
    }
}

