#scrollthing
{
    position: fixed;
    width: 100%;
    bottom: 10px;
    padding: 0px;
    margin: 0px; 
    overflow: hidden;
    display: flex;
    flex-direction: row;

    p {
        display: inline-block;
        color: var(--positive);
        font-family: $fonty;
        font-size: 10px;
        opacity: 60%;
        text-shadow: 0px 1px var(--theme-1-primary), 0px -1px var(--theme-2-primary), 1px 0px var(--theme-3-primary), -1px 0px var(--line-color);
        overflow: hidden;
        transition: color 1s ease-in-out;
    }

    div {
        position: absolute;
        left: 10px;
        opacity: 40%;
        color: var(--positive);
        text-shadow: 0px 1px var(--theme-1-primary), 0px -1px var(--theme-2-primary), 1px 0px var(--theme-3-primary), -1px 0px var(--line-color);
        font-family: $fonty;
        font-size: 10px;
    }
}

.rotate
{
    pointer-events: none;
    transform-style: preserve-3d;
    display: flex;
    flex-direction: row;
    align-content: center;
    align-items: center;
    transition: 1s ease-in;
}

.hover-bg
{
    margin: 0;
    padding: 0;
    height: 200vh; // change to 0 for expand?
    width: $box-size;
    
    display: flex;
    flex-direction: row;
    align-content: center;
    align-items: center;
    pointer-events: none;
    //pointer-events: all; ----------------------------------------------
    transform: translateZ($box-size / 2);
    transition: 1s ease-in;
}

#main h1 {
    color: var(--positive);
}

.logo img
{
    width: 150px;
    height: auto;
    float: left;
    margin-left: 30px;
    margin-top: 10px;
}

.message
{
    display: none;
    margin: auto;
    text-align: center;

    a {
        color: var(--positive);
    }
}

#particles-dark
{
    pointer-events: all;
    position: fixed;
    width: 100%;
    height: 100%;
    opacity: 50%;
    z-index:-4;
    clip-path: inset(0 0 0 0);
    transition: opacity ease-in 1.5s;
}

.light #particles-dark
{
    opacity: 0%;
    pointer-events: none;
}

#particles-light
{
    pointer-events: all;
    position: fixed;
    width: 100%;
    height: 100%;
    opacity: 50%;
    z-index:-4;
    clip-path: inset(0 0 0 0);
    transition: opacity ease-in 1.5s;
}

.dark #particles-light{
    opacity: 0%;
    pointer-events: none;
}

#games
{
    animation: floaty 3s ease-in-out 0.5s infinite;
}

#music
{
    animation: floaty 3.2s ease-in-out 0.0s infinite;
}

#art
{
    animation: floaty 2.8s ease-in-out 0.2s infinite;
}

#me
{
    animation: floaty 2.4s ease-in-out 0.8s infinite;
}

@keyframes floaty
{
    from{
        transform:translate(0,0px);
    }
    55%{
        transform:translate(0,15px);
    }
    to{
        transform:translate(0,0px);
    }
}

@keyframes bg_layer_fade
{
    from {
        opacity: 100%;
    }
    to {
        opacity: 0%;
    }
}

#constr {
    font-size: 200px;
    //opacity: 60%;
    width: 100%;
    text-align: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    pointer-events: none;
}